<template>
<v-system-bar app style="-webkit-app-region: drag">
  <span>{{tabMgr.get().fullTitle || tabMgr.get().title}}</span>
  <v-spacer></v-spacer>

  <div style="-webkit-app-region: none">
    <v-icon @click="minimize">mdi-square</v-icon>
    <v-icon @click="maximize">mdi-circle</v-icon>
    <v-icon @click="close">mdi-triangle</v-icon>
  </div>
</v-system-bar>
</template>

<script>
import { remote } from 'electron';

export default {
  methods: {
    minimize() {
      remote.BrowserWindow.getFocusedWindow().minimize();
    },
    maximize() {
      if (remote.BrowserWindow.getFocusedWindow().isMaximized()) {
        remote.BrowserWindow.getFocusedWindow().unmaximize();
        return;
      }

      remote.BrowserWindow.getFocusedWindow().maximize();
    },
    close() {
      if (!confirm('Вы действительно хотите закрыть TK2?')) {
        return;
      }

      remote.BrowserWindow.getFocusedWindow().close();
    }
  }
};
</script>
